@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400&display=swap');

:root {
  --black: rgba(0, 0, 0, 1);
  --bay-of-many: rgba(30, 53, 131, 1);
  --eucalyptus: rgba(65, 226, 150, 1);
  --white: rgba(255, 255, 255, 1);

  --font-size-s: 10px;
  --font-size-m: 24px;
  --font-size-l: 28px;
  --font-size-xl: 48px;

  --font-family-sansation-bold: "Sansation-Bold";
  --font-family-sansation_light-light: "Sansation Light-Light";
}

@font-face {
  font-family: "Sansation Light-Light";
  font-style: normal;
  font-weight: 300;
  src: url("https://fonts.animaapp.com/SansationLight-Light") format("truetype");
}
@font-face {
  font-family: "Sansation-Bold";
  font-style: normal;
  font-weight: 700;
  src: url("https://fonts.animaapp.com/Sansation-Bold") format("truetype");
}

.container-fluid
{
  background-color: var(--bay-of-many);
  padding:0;
  margin-top:-10px;
  width:100%;
  overflow: hidden;
}
.text-1
{
   color:white;
   font-size:35px;
   font-family: 'Roboto', sans-serif;
   font-weight: 700;
   margin:5% 0 2% 10%;

}

.text-1 .green
{
  color:#4bdaa1;
}

.para
{
  color:white;
  font-family: 'Roboto', sans-serif;
  margin:1% 0 1% 10%;
  font-size:15px;
  font-weight: 300;
}
.para-1
{
  color:white;
  font-family: 'Roboto', sans-serif;
  margin:1% 0 3% 10%;
  font-size:15px;
  font-weight: 300;
}
.text-2
{
  color:white;
   font-size:30px;
   font-family: 'Roboto', sans-serif;
   font-weight: 600;
   text-align: center;
   margin-bottom: 2%;
   
}

.buttons
{
  display: flex;
  justify-content: center;
  margin-bottom:3%;
}

.buttons button{
  font-family: 'Roboto', sans-serif;
  font-size:12.5px;
  font-weight: bold;
  color: #464c7f;

}

.buttons :nth-child(1)
{
  margin-right:3%;
}
.buttons :nth-child(2)
{
  margin-left:3%;
}
.inner-part
{
  display: flex;
  justify-content: center;
  margin-bottom:5%;
}

.leftside
{
  color:white;
  font-family: 'Roboto', sans-serif;
  text-align: right;
  margin-right:3%;
}

.leftside h3
{
  font-size:25px;
  font-weight: 600;
   
}

.leftside .green{
  color:#4bdaa1;
}
.leftside .get
{
  line-height: 70pt;
}

.v1
{
  border-left: 3px solid white;
  height: 230px;
  margin-left:5%;
  margin-top:-10px;
  margin-bottom:10px;
}
.Rightside
{
  margin-left:15px;
  padding:0 0 0 5%;
}
.tree
{
  display: flex;
}
.tree img{
  width:40px;
  height:45px;
}
.tree p 
{
  color:white;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  font-size: 14px;
  margin:7px 0 0 5px;
}
.coins
{
  display: flex;
  margin-top:15px;
}
.coins img{
  width:40px;
  height:45px;
}
.coins p 
{
  color:white;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  font-size: 14px;
  margin:5px 0 0 10px;
}

.vault
{
  display: flex;
  margin-top:15px;
}
.vault img{
  width:30px;
  height:35px;
}
.vault p 
{
  color:white;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  font-size: 14px;
  margin:0 0 0 17px;
}
.oracle
{
  display: flex;
  margin-top:15px;
}
.oracle img{
  width:40px;
  height:45px;
}
.oracle p 
{
  color:white;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  font-size: 14px;
  margin:0 0 0 10px;
}

.Bottom
{
  display: none;
}
/* Footer */
.Footer
{
  width:100%;
  height: 100%;
}
.footer-bg
{
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}
