@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400&display=swap');
.header{
    margin:10px 0 10px 20px;
}
.header .logo-img
{
    width:150px;
    height:50px;
}
.header-right
{
    display: flex;
    float: right;
    margin:15px 20px 10px 0;
}
.header-right .discord-logo
{
   margin-right:5px;
   width: 18px;
  height: 18px;
  margin-top:2px;
}

.header-right .Telegram-logo
{
    width: 18px;
     height: 18px;
     margin-left:5px;
     margin-top:2px;
}

.dropdown
{
    width:45px;
    height:30px;
    margin-top:-13px;
    
}
.dropdown-icon
{
    color:white;
    font-size:35px !important;
}
.dropdown-toggle:focus,
.dropdown-toggle:active {
    outline: none !important;
    border:none !important;
  }
.dropdown-toggle::after {
    content: none !important;
}

.header-right button 
{
    font-family: 'Roboto', sans-serif;
    font-size: 12px;
    font-weight: 700;
    color: #464c7f;
    
}

.header-right .connect
{
    margin-right: 30px;
    margin-top: -5px;
}


@media(max-width:414px)
{
    .header-right button {
        font-size: 10px;
    }
    .header-right .connect {
        margin-right: 22px;
    }
    .header .logo-img {
        width: 135px;
        height: 50px;
    }
}
@media(max-width:375px)
{
    .header-right button {
        font-size: 7px !important;

    }
    .header-right .connect {
        margin-right: 13px;
    }
}
@media(max-width:360px)
{
   
    .header-right .connect {
        margin-right: 4px;
    }
}

